
import { Switch, useHistory } from "react-router-dom"

import { AuthProvider, StaffRoute } from "@meddbriefer/mdb-auth";
// , PrivateRoute
import { useFirebaseProject, FirebaseContext } from "@meddbriefer/mdb-firebase"

import {GetReview } from "@meddbriefer/cond1-debriefing-ui"
import {
  DebLog,
  UnauthedLayout,
  computeReviewTitle,
  SaveReviewModal,
} from "@meddbriefer/cond2-debriefing-ui";

import { LogChooser } from "@meddbriefer/log-chooser"

import { environment } from "./environments/environment";


const App = (props) => {
    const {auth, db } = useFirebaseProject(environment.firebaseProject)
    let history = useHistory()
    window.db = db;

    function redirectToCond1Debriefing(debriefID) {
      const newURL = `/debriefing/${debriefID}`
      history.push(newURL)
    }

    function redirectToCond2Debriefing(debriefID) {
      const newURL = `/debriefing2/${debriefID}`;
      history.push(newURL);
    }

    return (
      <FirebaseContext.Provider
        value={{
          auth,
          db,
        }}
      >
        <AuthProvider>
          <Switch>
            <StaffRoute
              exact
              path="/debriefing/:logID"
              authFlowLayout={UnauthedLayout}
              component={GetReview}
            />
            <StaffRoute
              path="/debriefing2/:debriefingID"
              authFlowLayout={UnauthedLayout}
            >
              <DebLog
                computeTitle={computeReviewTitle}
                saveButtonLabel="Save"
                saveModal={SaveReviewModal}
              />
            </StaffRoute>

            {/*default (catchall) route*/}
            <StaffRoute authFlowLayout={UnauthedLayout}>
              <LogChooser
                redirectToCond1Debriefing={redirectToCond1Debriefing}
                redirectToCond2Debriefing={redirectToCond2Debriefing}
              />
            </StaffRoute>
          </Switch>
        </AuthProvider>
      </FirebaseContext.Provider>
    );
}


export default App;